import switchButton from '@/components/switchButton-auto.vue';
let _this,_quoteNumber
export default {
	components:{
	   switchButton	
	},
	data() {
		return {
			newPrice:'',
			remark:'',
			unitIndex: 1,
			unitPrice:'',
			unit: '',
			unitPriceDecimal:''
		};
	},
	onLoad:function(options){
		_this = this
		_quoteNumber =  options.quotationNumber
		this.unitPrice  = options.unitPrice
		this.newPrice = options.unitPriceDecimal
		this.unit = this.unitPrice.substring(this.unitPrice.length -3)
	},
	methods:{
		tapSwitch:function(e){
			this.unitIndex = e
			console.log(this.unitIndex)
		},
		submit:function(){
			if (_this.newPrice<0 || _this.newPrice==''){
				uni.showToast({
					title: '价格不能小于零',
					icon: 'none'
				});
				return
			}
			let data={
				quotationNumber: _quoteNumber    ,			//报价单号
				directorUnitPrice:  _this.newPrice   ,		//价格
				remarks: _this.remark  			//备注

			}
			let url = this.Api.directorModifyPrice
			uni.showModal({
				title: '确认推送',
				content: '确认价格合适',
				showCancel: true,
				cancelText: '取消',
				confirmText: '确认',
				success: res => {
					if (res.confirm) {
					   console.log('用户点击确定');
					   uni.showLoading({
					   	  title:'提交中',
					      mask: true
					   })
					   this.myRequest(data,url,'get').then(res => {
					     console.log(res);
					     if (res.data.status == 0){
					   	 uni.hideLoading()
					   	  var pages = getCurrentPages();
					   	  var currPage = pages[pages.length - 1]; //当前页面
					   	  var prevPage = pages[pages.length - 2]; //上一个页面
					   	  //直接调用上一个页面的setData()方法，把数据存到上一个页面中去
					   	  
					   	  prevPage.setData({
					   	     isDoRefresh:true
					   	  })
					   	 
					   	  
					   	  uni.navigateBack({
					   	  	delta: 1
					   	  });
					   	  
					     }
					   }).catch(err => {
					     wx.showToast({
					       title: err.data.errMsg,
					       icon: 'none'
					     });
					   });
					   
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				},
				fail: (err) => {
					console.log(err)
				},
				complete: () => {}
			});
			
			
		}
	}
	
}
